form {
    margin-top: 150px;
    position: center;
    background: #fff;
    width: 60vh;
    color: #3e0a79;
    border-radius: 10px;
    box-shadow: 0 0 12px 0  rgba(19, 224, 224, 0.5); 
} 

.form {
    width: 100%;
    margin: 20px;
}

form .grupo {
    position: relative;
    margin: 45px;

}

input,
textarea {
    
    background: none;
    color: #3e0a79;
    font-size: 18px;
    padding: 10px 10px 10px 5px;
    display: block;
    width: 100%;
    border: none;
    border-bottom: 1px solid var(--colorTextos);
    resize: none;
    transition: all 0.3s ease-in-out;
}

input:focus,
textarea:focus {
    outline: none;
    color: #3e0a79;
}

input:focus~label,
input:valid~label,
textarea:focus~label,
textarea:valid~label {
    position: absolute;
    top: -14px;
    font-size: 12px;
    color: #3e0a79;
;
}


input:focus~label,
input:invalid~label,
textarea:focus~label,
textarea:invalid~label {
    position: absolute;
    top: -14px;
    font-size: 12px;
}

label {
    color: #3e0a79;
    font-size: 16px;
    position: absolute;
    left: 5px;
    top: 10px;
    transition: 0.5s ease all;
    pointer-events: none;
}

input:focus~.barra::before,
textarea:focus~.barra::before {
    width: 100%;
}

.barra {
    position: relative;
    display: block;
    width: 100%;
}

.barra::before {
    content: '';
    height: 2px;
    width: 0%;
    bottom: 0;
    position: absolute;
    background: linear-gradient(to right, white , #3e0a79);
    transition: 0.3s ease all;
    left: 0%;
}

.boton-submit{
    width: 100px;
    height: 40px;
    font-style: bold;
    text-decoration: none;
    border-radius: 4px 4px 4px 4px;
    margin-left: 50%;
    margin-top: 20px;
    background: rgba(53,240,208,2);
    color: #3e0a79;
    outline: none;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
    font-size: 1.2rem;
    font-weight: bold;
    box-shadow: 0px 0px 10px rgba(19, 224, 224, 0.5);
    border: none;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin-bottom: 20px;
}

.boton-submit:hover{
    background: #3e0a79;
    color: white;
}

@media screen and (max-width:1100px) {
    form {
        width: 100% ;
    }
}

